import React from 'react'
import 'antd/dist/antd.css'
import { css } from '@emotion/core'
import '../grid.css'
import '../style.css'
import { Button } from 'antd'
import { Link } from 'Config/routes'

const MainHome = () => {
  return(
    <div className="container">
      <div css={css`
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 100vh;
        grid-gap: 3em;
        align-items: center;
        @media(max-width: 700px) {
          grid-template-columns: 1fr;
          grid-template-rows: auto;
        }
      `}>
        <div css={css`
        position: relative;
          img.maps {
            width: 600px;
            margin-top: 80px;
          }
          .car,.car1, .motorcycle {
            position: absolute;
            width: 150px;
            right: 20px;
          }

          .car{
            top: -30px;
          }

          .car1 {
            bottom: -90px;
            right: 300px;
            width: 100px;
          }
          .motorcycle{
            top: -30px;
            left: 20px;
          }
        `}>
          <img className="maps" src="/static/map.png" alt=""/>
          <img className="motorcycle" src="/static/icons/motor1.png" alt=""/>
          <img className="car" src="/static/icons/bigcar.png" alt=""/>
          <img className="car1" src="/static/icons/car.png" alt=""/>
        </div>
        <div css={css`
          button {
            margin-top: 2em;
            transition: 0.3s !important;
            &:hover {
              box-shadow: 0px 5px 10px rgba(24, 144, 255, 0.2) !important;
              transform: translateY(-3px) !important;
            } 
          }
        `}>
          <h1 css={css`
            font-size: 2.5em;
            color: #1890ff;
            text-shadow: 0px 5px 10px rgba(24, 144, 255, 0.2);
          `}>WELCOME TO MARKIR.ID</h1>
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Iusto iste similique culpa, accusamus dignissimos aperiam ab? Quam, esse exercitationem minus maiores nobis ullam distinctio aut id, quod, accusantium fugit molestiae.</p>
          <Link route="login">
            <Button type="primary" ghost>LOGIN HERE</Button>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default MainHome